import { Dispatch, SetStateAction } from "react"
import { CqsItem } from "@rp/common/api-types"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { Box, IconButton } from "@rp/react/web/mui/core"
import { Add, Delete, Refresh } from "@rp/react/web/mui/icons"
import { WatchlistDataTabsDownloadCsv } from "../WatchlistDataTabs/WatchlistDataTabsDownloadCsv"
import { SmartGridColumn } from "./WatchlistSmartGrid"
import { generateSmartGridEmptyColumn } from "./generateSmartGridEmptyColumn"

type Props = {
  downloadCSV: () => void
  setSelectedColumn: Dispatch<SetStateAction<SmartGridColumn | null | undefined>>
  smartGridColumns: Array<SmartGridColumn>
  setSmartGridColumns: Dispatch<SetStateAction<Array<SmartGridColumn>>>
  entities: Array<CqsItem>
}

const WATCHLIST_SMART_GRID_ADD_COLUMN_ID = "watchlist-smart-grid-add-column-button"

function WatchlistSmartGridActions({
  setSelectedColumn,
  smartGridColumns,
  entities,
  downloadCSV,
  setSmartGridColumns,
}: Props) {
  return (
    <>
      <RPTooltip title="Add Column" placement="bottom">
        <IconButton
          id={WATCHLIST_SMART_GRID_ADD_COLUMN_ID}
          onClick={() => setSelectedColumn(null)}
          size="small"
        >
          <Add sx={{ fontSize: 16 }} />
        </IconButton>
      </RPTooltip>
      <WatchlistDataTabsDownloadCsv disabled={!smartGridColumns.length} onDownload={downloadCSV} />
      <RPTooltip title="Refresh Data" placement="bottom">
        <Box component="span">
          <IconButton
            disabled={!smartGridColumns.length}
            onClick={() => {
              setSmartGridColumns(prev => {
                return prev.map(column => {
                  return generateSmartGridEmptyColumn({
                    id: column.id,
                    title: column.title,
                    prompt: column.prompt,
                    scope: column.scope,
                    entities,
                  })
                })
              })
            }}
            size="small"
          >
            <Refresh sx={{ fontSize: 16 }} />
          </IconButton>
        </Box>
      </RPTooltip>
      <RPTooltip title="Clear Table" placement="bottom">
        <Box component="span">
          <IconButton
            disabled={!smartGridColumns.length}
            onClick={() => setSmartGridColumns([])}
            size="small"
          >
            <Delete sx={{ fontSize: 16 }} />
          </IconButton>
        </Box>
      </RPTooltip>
    </>
  )
}

export { WatchlistSmartGridActions, WATCHLIST_SMART_GRID_ADD_COLUMN_ID }
